@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

*{
  margin: 0;
  padding: 0;
}

body{
  background-color: #141414;

  overflow-y: hidden;
}

header{
  box-shadow: 2px 2px 32px 1px rgba(0, 0, 10, .5);

  width: 100%;
}

.NetflixLogo{
  width: 8rem;

  margin-top: 1rem;
  margin-left: 3rem;
}

main{
  width: 100vw;
  height: 100vh;
}

.ajuste{
  width: 10rem;

  margin-right: 2rem;
  margin-top: 2rem;
}

.text1{
  color: white;

  font-family: Roboto;
  font-weight: 100;

  margin-bottom: 2rem;

  display: flex;
  justify-content: center;
  align-items: center;
}

.center{
  text-align: center;
  transform: translateY(25%);

  height: 100vh;
}

.gerenciar{
  margin-top: 6rem;

  background: none;
  border: none;

  opacity: 40%;

  cursor: pointer;

  border: 1px solid white;

  padding: .5rem;

  width: 15rem;

  font-size: 1.4rem;

  color: white;
}

.gerenciar:hover{
  opacity: 100%;
}

.ajuste:hover{
  outline: auto;
}